<script>
  import { elasticOut } from "svelte/easing";

  // Contains an array of Javascript objects.
  // Each object contains: 
  // [
  //    {
  //         className: string
  //         image: string (url to image)
  //         members: [{
  //           name: string
  //           major: string
  //           pixelLocationX: number (pixel location on class picture)
  //           pixelLocationY: number (same but on y axis)
  //           linkedIn: string (the url)
  //         },
  //     ...]
  //    },
  // ...]
  // basically an ar
  // order from newest to oldest
  export let pledgeClasses;

  let visible = true;

  let classIndex = 0;
  let curClass = pledgeClasses[classIndex];

  let my, mx;

  function handleMousemove(event) {
		mx = event.clientX;
		my = event.clientY;
	}
</script>

<style>
  .class-img-container {
    position: relative;
    width: 70vw;
    max-width: calc(100vw - 10em);
    height: 70vh;
    overflow: hidden;
  }

  .class-img {
    position: absolute;
    top: -9999px;
    left: -9999px;
    right: -9999px;
    bottom: -9999px;
    height: 100%;
    margin: auto;
    z-index: 1;
    transform: scale(3);
    transform-origin: 50% 40%;
  }
</style>

<div class="pledge-classes">
  <div class="class-img-container">
    <img
      on:mousemove={handleMousemove}
      class="class-img"
      src="{curClass.image}"
      alt="{curClass.className}"
    >
  </div>
</div>
<p>mx:{mx}</p>
<p>my:{my}</p>
